<script>
export default {
  name: 'Floaters',
  data () {
    return {
      floaterStyles: {},
      floaterLoaded: false
    }
  },
  mounted () {
    this.$nextTick(() => {
      window.addEventListener('resize', this.resizeCallback)
      this.resizeCallback()
    })
  },
  methods: {
    resizeCallback () {
      let scale = Math.max(
        window.innerWidth / this.$el.clientWidth,
        window.innerHeight / this.$el.clientHeight
      )
      if (scale === Infinity) {
        setTimeout(this.resizeCallback, 10)
      } else {
        this.floaterStyles = {
          transform: `translate(-50%, -50%) scale(${scale})`
        }
        this.floaterLoaded = true
      }
    }
  }
}
</script>

<style lang="sass" scoped>
  .header-img
    background: white
    position: relative
    left: 50%
    top: 50%
    transform-origin: center center
    transform: translate(-50%, -50%)
    opacity: 0

  .floater-loaded
    opacity: 1
    transition: opacity 1.5s ease
    transition-delay: 2.2s
</style>



